  max-width: 100%;
  display: block;
  height: auto; }

/*На стандартных экранах бургер спрятан*/
.header__burger {
  display: none; }

/*Стили для основного меню и его пунктов*/
.header__list {
  position: relative;
  z-index: 2; }

@media (max-width: 991px) {
  /*Чтобы не скролилась страница под бургером меню*/
